import { useState } from 'react'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { z } from 'zod'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Save, Settings as SettingsIcon } from 'lucide-react'

const storeSettingsSchema = z.object({
  storeName: z.string().min(1, 'Store name is required'),
  storeDescription: z.string().min(1, 'Store description is required'),
  storeEmail: z.string().email('Invalid email address'),
  storePhone: z.string().min(1, 'Phone number is required'),
  storeAddress: z.string().min(1, 'Address is required'),
})

const paymentSettingsSchema = z.object({
  stripePublicKey: z.string().min(1, 'Stripe public key is required'),
  stripeSecretKey: z.string().min(1, 'Stripe secret key is required'),
  paypalClientId: z.string().optional(),
  paypalClientSecret: z.string().optional(),
})

const shippingSettingsSchema = z.object({
  freeShippingThreshold: z.number().min(0, 'Must be a positive number'),
  standardShippingRate: z.number().min(0, 'Must be a positive number'),
  expressShippingRate: z.number().min(0, 'Must be a positive number'),
})

type StoreSettings = z.infer<typeof storeSettingsSchema>
type PaymentSettings = z.infer<typeof paymentSettingsSchema>
type ShippingSettings = z.infer<typeof shippingSettingsSchema>

export default function Settings() {
  const [activeTab, setActiveTab] = useState('store')
  const [isLoading, setIsLoading] = useState(false)

  const storeForm = useForm<StoreSettings>({
    resolver: zodResolver(storeSettingsSchema),
    defaultValues: {
      storeName: 'Zupp.store',
      storeDescription: 'Your premium e-commerce store',
      storeEmail: 'contact@zupp.store',
      storePhone: '+1 (555) 123-4567',
      storeAddress: '123 Commerce St, Business City, BC 12345',
    },
  })

  const paymentForm = useForm<PaymentSettings>({
    resolver: zodResolver(paymentSettingsSchema),
    defaultValues: {
      stripePublicKey: '',
      stripeSecretKey: '',
      paypalClientId: '',
      paypalClientSecret: '',
    },
  })

  const shippingForm = useForm<ShippingSettings>({
    resolver: zodResolver(shippingSettingsSchema),
    defaultValues: {
      freeShippingThreshold: 100,
      standardShippingRate: 9.99,
      expressShippingRate: 19.99,
    },
  })

  const onStoreSubmit = async (data: StoreSettings) => {
    setIsLoading(true)
    try {
      // API call to save store settings
      console.log('Store settings:', data)
      // await api.put('/admin/settings/store', data)
    } catch (error) {
      console.error('Failed to save store settings:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const onPaymentSubmit = async (data: PaymentSettings) => {
    setIsLoading(true)
    try {
      // API call to save payment settings
      console.log('Payment settings:', data)
      // await api.put('/admin/settings/payment', data)
    } catch (error) {
      console.error('Failed to save payment settings:', error)
    } finally {
      setIsLoading(false)
    }
  }

  const onShippingSubmit = async (data: ShippingSettings) => {
    setIsLoading(true)
    try {
      // API call to save shipping settings
      console.log('Shipping settings:', data)
      // await api.put('/admin/settings/shipping', data)
    } catch (error) {
      console.error('Failed to save shipping settings:', error)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-3xl font-bold tracking-tight">Settings</h1>
        <p className="text-muted-foreground">
          Configure your store settings and preferences
        </p>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-4">
        <TabsList>
          <TabsTrigger value="store">Store</TabsTrigger>
          <TabsTrigger value="payment">Payment</TabsTrigger>
          <TabsTrigger value="shipping">Shipping</TabsTrigger>
        </TabsList>

        <TabsContent value="store">
          <Card>
            <CardHeader>
              <CardTitle>Store Information</CardTitle>
              <CardDescription>
                Update your store's basic information and contact details
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={storeForm.handleSubmit(onStoreSubmit)} className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="storeName">Store Name</Label>
                    <Input
                      id="storeName"
                      {...storeForm.register('storeName')}
                    />
                    {storeForm.formState.errors.storeName && (
                      <p className="text-sm text-destructive">
                        {storeForm.formState.errors.storeName.message}
                      </p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="storeEmail">Store Email</Label>
                    <Input
                      id="storeEmail"
                      type="email"
                      {...storeForm.register('storeEmail')}
                    />
                    {storeForm.formState.errors.storeEmail && (
                      <p className="text-sm text-destructive">
                        {storeForm.formState.errors.storeEmail.message}
                      </p>
                    )}
                  </div>
                </div>
                <div className="space-y-2">
                  <Label htmlFor="storeDescription">Store Description</Label>
                  <Input
                    id="storeDescription"
                    {...storeForm.register('storeDescription')}
                  />
                  {storeForm.formState.errors.storeDescription && (
                    <p className="text-sm text-destructive">
                      {storeForm.formState.errors.storeDescription.message}
                    </p>
                  )}
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="storePhone">Phone Number</Label>
                    <Input
                      id="storePhone"
                      {...storeForm.register('storePhone')}
                    />
                    {storeForm.formState.errors.storePhone && (
                      <p className="text-sm text-destructive">
                        {storeForm.formState.errors.storePhone.message}
                      </p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="storeAddress">Address</Label>
                    <Input
                      id="storeAddress"
                      {...storeForm.register('storeAddress')}
                    />
                    {storeForm.formState.errors.storeAddress && (
                      <p className="text-sm text-destructive">
                        {storeForm.formState.errors.storeAddress.message}
                      </p>
                    )}
                  </div>
                </div>
                <Button type="submit" disabled={isLoading}>
                  <Save className="mr-2 h-4 w-4" />
                  {isLoading ? 'Saving...' : 'Save Changes'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="payment">
          <Card>
            <CardHeader>
              <CardTitle>Payment Settings</CardTitle>
              <CardDescription>
                Configure payment gateways and processing options
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={paymentForm.handleSubmit(onPaymentSubmit)} className="space-y-4">
                <div className="space-y-4">
                  <h3 className="text-lg font-medium">Stripe Configuration</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="stripePublicKey">Stripe Public Key</Label>
                      <Input
                        id="stripePublicKey"
                        type="password"
                        {...paymentForm.register('stripePublicKey')}
                      />
                      {paymentForm.formState.errors.stripePublicKey && (
                        <p className="text-sm text-destructive">
                          {paymentForm.formState.errors.stripePublicKey.message}
                        </p>
                      )}
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="stripeSecretKey">Stripe Secret Key</Label>
                      <Input
                        id="stripeSecretKey"
                        type="password"
                        {...paymentForm.register('stripeSecretKey')}
                      />
                      {paymentForm.formState.errors.stripeSecretKey && (
                        <p className="text-sm text-destructive">
                          {paymentForm.formState.errors.stripeSecretKey.message}
                        </p>
                      )}
                    </div>
                  </div>
                </div>
                <div className="space-y-4">
                  <h3 className="text-lg font-medium">PayPal Configuration (Optional)</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="paypalClientId">PayPal Client ID</Label>
                      <Input
                        id="paypalClientId"
                        {...paymentForm.register('paypalClientId')}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="paypalClientSecret">PayPal Client Secret</Label>
                      <Input
                        id="paypalClientSecret"
                        type="password"
                        {...paymentForm.register('paypalClientSecret')}
                      />
                    </div>
                  </div>
                </div>
                <Button type="submit" disabled={isLoading}>
                  <Save className="mr-2 h-4 w-4" />
                  {isLoading ? 'Saving...' : 'Save Changes'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="shipping">
          <Card>
            <CardHeader>
              <CardTitle>Shipping Settings</CardTitle>
              <CardDescription>
                Configure shipping rates and policies
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={shippingForm.handleSubmit(onShippingSubmit)} className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="freeShippingThreshold">Free Shipping Threshold ($)</Label>
                    <Input
                      id="freeShippingThreshold"
                      type="number"
                      step="0.01"
                      {...shippingForm.register('freeShippingThreshold', { valueAsNumber: true })}
                    />
                    {shippingForm.formState.errors.freeShippingThreshold && (
                      <p className="text-sm text-destructive">
                        {shippingForm.formState.errors.freeShippingThreshold.message}
                      </p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="standardShippingRate">Standard Shipping Rate ($)</Label>
                    <Input
                      id="standardShippingRate"
                      type="number"
                      step="0.01"
                      {...shippingForm.register('standardShippingRate', { valueAsNumber: true })}
                    />
                    {shippingForm.formState.errors.standardShippingRate && (
                      <p className="text-sm text-destructive">
                        {shippingForm.formState.errors.standardShippingRate.message}
                      </p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="expressShippingRate">Express Shipping Rate ($)</Label>
                    <Input
                      id="expressShippingRate"
                      type="number"
                      step="0.01"
                      {...shippingForm.register('expressShippingRate', { valueAsNumber: true })}
                    />
                    {shippingForm.formState.errors.expressShippingRate && (
                      <p className="text-sm text-destructive">
                        {shippingForm.formState.errors.expressShippingRate.message}
                      </p>
                    )}
                  </div>
                </div>
                <Button type="submit" disabled={isLoading}>
                  <Save className="mr-2 h-4 w-4" />
                  {isLoading ? 'Saving...' : 'Save Changes'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}